import { Injectable } from '@angular/core';

@Injectable()
export class StudentService {

  constructor() { }

  private students: Student[] = [
    new Student(1, '', '', '', '', '18888888888', '123456@qq.com'),
    new Student(2, '', '', '', '', '18888888888', '123456@qq.com'),
    new Student(3, '', '', '', '', '18888888888', '123456@qq.com'),
    new Student(4, '', '', '', '', '18888888888', '123456@qq.com'),
    new Student(5, '', '', '', '', '18888888888', '123456@qq.com')
  ];

  getStudents(): Student[] {
    return this.students;
  }
  getStudent(id: number): Student {
    var student =this.students.find(student => student.id ==id);;
    if(!student){
      student =new Student(0, '', '', '', '', '', '');
    }
    return student;
  }

}

export class Student {
  constructor(public id: number,
              public name: string,
              public gender: string,
              public department: string,
              public major: string,
              public telephone: string,
              public email: string) {

  }
}



