import { Component, OnInit } from '@angular/core';
import {NavigationEnd, Router} from '@angular/router';
import 'rxjs/add/operator/filter';

@Component({
  selector: 'app-content',
  templateUrl: './content.component.html',
  styleUrls: ['./content.component.scss']
})
export class ContentComponent implements OnInit {

  pageTitle = '';
  pageDesc = '';
  pageBoard='';
  constructor(public router: Router) {
    router.events
      .filter(event => event instanceof NavigationEnd)
      .subscribe((event: NavigationEnd) => {
          if (event.url === '/test/welcome') {
            this.pageTitle = '';
            this.pageDesc = '';
            this.pageBoard='';
          } else if (event.url.startsWith('/test/student')) {
            this.pageTitle = '';
            this.pageDesc = '';
            this.pageBoard=''
          }else if (event.url.startsWith('/test/teacher')) {
            this.pageTitle = '';
            this.pageDesc = '';
            this.pageBoard=''
          }else if (event.url.startsWith('/test/role')) {
            this.pageTitle = '';
            this.pageDesc = '';
            this.pageBoard=''
          }else if (event.url.startsWith('/test/user')) {
            this.pageTitle = '';
            this.pageDesc = '';
            this.pageBoard=''
          }else if (event.url.startsWith('/test/department')) {
            this.pageTitle = '';
            this.pageDesc = '';
            this.pageBoard=''
          }else if (event.url.startsWith('/test/course')) {
            this.pageTitle = '';
            this.pageDesc = '';
            this.pageBoard=''
          }else if (event.url.startsWith('/test/teachingarrange')) {
            this.pageTitle = '';
            this.pageDesc = '';
            this.pageBoard=''
          } else if (event.url.startsWith('/test/historyrecord')) {
            this.pageTitle = '';
            this.pageDesc = '';
            this.pageBoard=''
          }else if (event.url.startsWith('/test/parameter')) {
            this.pageTitle = '';
            this.pageDesc = '';
            this.pageBoard=''
          }else if (event.url.startsWith('/test/rankinglist')) {
            this.pageTitle = '';
            this.pageDesc = '';
            this.pageBoard=''
          }else if (event.url.startsWith('/test/permission')) {
            this.pageTitle = '';
            this.pageDesc = '';
            this.pageBoard=''
          }else if (event.url.startsWith('/test/askforleave')) {
            this.pageTitle = '';
            this.pageDesc = '';
            this.pageBoard=''
          }else if (event.url.startsWith('/test/test1')) {
            this.pageTitle = '';
            this.pageDesc = '';
            this.pageBoard=''
          }
        });
  }

  ngOnInit() {
  }

}



